.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.slider {
  width: 200px;
  line-height: 40px;;
  display: inline-flex;
}

.slider-comp{
  vertical-align: middle;
}
.element-container{
  background-color: #fffcf1;
  border-bottom: 1px solid #bbb7b7;
  height: 90vh;
}
.elements{
  height: 150px;
  display: flex;               
  flex-direction: row;          
  justify-content: center; 
}
.controls{
  padding-bottom: 15px;
  border-bottom: 6px solid #b6bcdb;
}
:focus {
  outline: 0;
}
button:focus {
  outline: none;
}
.node{
  display: flex;
  width:40px;
  border:1px solid #0ba1b5;
  padding: 5px;
  justify-content: center;
  border-radius: 1px;
  position:absolute;
  box-shadow: 0 3px 3px rgba(0,0,0,0.16), 0px 0px 2px rgba(0,0,0,0.23);
  background-color: #4DD0E1;
  color: #4a4a4a;
}

.node:hover{
  cursor: pointer;
  background-color: #027080;
  color: #dadada;
}
.tree{
  position: relative;
}
#svgContainer { 
	z-index: -10;
	width: 98%;
  height: 80vh;
  position:absolute;
  opacity: 0.5;
}

.tree-cointainer{
  padding: 10px;
  position: relative;
  overflow: auto;
  height: 85vh;
  border-bottom: 6px solid #b6bcdb;
}